// Functional Imports
import { useRoutes } from 'react-router-dom';
import { Suspense } from 'react';

import { Navigate, useParams } from 'react-router-dom';
import { useHttpClientWithNavigate } from '../utility/http-hook';

// Componet Imports
import Login from '../componets/Login/Login';
import Loading from '../componets/Loading/Loading';

// Styled Imports

function LoginPage(){

    const routes = useRoutes([
        {
            index: true,
            element: <Login />
        },
        {
            path: ':userId/change-user', element: <RedirectToBookingFormView />
        },
        { 
            path: '*', 
            element: (
                <Suspense fallback={<Loading fallback />}>
                    <Navigate to='/error' state={{ statusText: 'Page Not Found!', status: 404 }} />
                </Suspense>
            )
        }    
    ])

    return routes;
}

function RedirectToBookingFormView(props){
    const { userId } = useParams();
    const sendRequest = useHttpClientWithNavigate();

    sendRequest({ url: '/auth/' + userId + '/change-user'  })
    .then(_ => {
        window.location = '/'
    })
    .catch(msg => {
        console.log(msg);
        alert(msg.msg)
    })

    return <h1>Loading user!!</h1>
}



export default LoginPage;