// Functional Imports
import React, { useEffect } from 'react';
import cssNameSimplifier from '../../utility/css-name-simplifier';
import { useHttpClientWithNavigate } from '../../utility/http-hook';
import { useDispatch } from 'react-redux';
import { AlertSliceActions } from '../../store/alert-slice';
import isEmpty from '../../utility/is-empty';
import { useNavigate } from 'react-router-dom';

// Componet Imports
import Container from '../UI/Container/Container';
import { Divider, Chip } from '@mui/material';

// Styled Imports
import importedStyles from './Login.module.css';
import concordeLogo from '../../Assets/Img/concorde-logo.png'
import MicrosoftIcon from '@mui/icons-material/Microsoft';
import GoogleIcon from '@mui/icons-material/Google';

const styles = cssNameSimplifier(importedStyles);

function Login(props) {
    const dispatcher = useDispatch();
    const sendRequest = useHttpClientWithNavigate();
    const navigate = useNavigate();


    useEffect(() => {
        (async () => {
            try{
                const response = await sendRequest({ url: '/auth/redirect-messages' });

                if(!isEmpty(response))
                    dispatcher( AlertSliceActions.showAlert({ message: response.msg, severity: 'error' }) )
            } catch( err ){
                dispatcher( AlertSliceActions.showAlert({ message: 'Failed to fetch any errors', severity: 'error' }) );
                navigate('/error', { state: { status: err.status, msg: err.msg } })
            }
        })();        
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    function authenticate(oauth){
        // TODO: Change to production
        window.location.href = `https://backend.concorde.in:8080/auth/${oauth}`;
    }

    return (
        <div className={styles.loginBoxConatiner}>
            <Container>
                <header className={styles.header}>
                    <div className={styles.logoContainer}>
                        <img src={concordeLogo} alt="Concorde Logo" />
                    </div>
                    <h2>Employee Login!</h2>
                    <div className={styles.loginOptionsGroup} onClick={() => authenticate('microsoft')}>
                        <MicrosoftIcon sx={{ fontSize: 60 }}/>
                        <p>Microsoft</p>
                    </div>
                    {/* <p>-- OR --</p> */}
                    <Divider variant='fullWidth' sx={{ width: 140 }}>
                        <Chip label='OR' />
                    </Divider>
                    <div className={styles.loginOptionsGroup} onClick={() => authenticate('google')}>
                        <GoogleIcon sx={{ fontSize: 60 }} />
                        <p>Google</p>
                    </div>
                </header>
            </Container>
        </div>
    );
}

export default Login;